    margin: 0;
    padding: 0;
    float: left
}

#styleSelector .nav-tabs .nav-item a {
    color: #37474f;
    text-align: center;
    font-weight: 500;
    font-size: 13px;
    padding: 0.50rem 1rem;
    border-radius: 2px;
    border: none;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out
}

#styleSelector .nav-tabs .nav-item a.active {
    color: #fff;
    background: #448aff
}

